<template>
  <el-container style="border:2px solid #C0C0C0;border-radius:10px;height: 600px; width: 1200px; margin: auto">
    <!-- 页面顶部 -->
    <el-header class="logo">
<el-image
      style="width: 55px; height: 55px;margin:5px;float:left"
      :src="url"
      :fit="contain"></el-image>
<div style="float:left">任务列表
</div>
    </el-header>
<el-container style="height: 500px; width: 1200px; margin: auto">
      <el-aside width="200px"
              style="background-color:rgb(238, 241, 246)">
      <br /><br />
      <el-button  type="primary" @click="select1()"> 快递代取 </el-button>
      <br /><br />
      <el-button  type="primary" @click="select2()"> 文件代送 </el-button>
      <br /><br />
      <el-button  type="primary" @click="select3()"> 食堂代买</el-button>
      <br /><br />
      <el-button  type="primary" @click="select4()"> 物品代购</el-button>
      <br /><br />
      <el-button  type="primary" @click="select5()"> 其他</el-button>
      <br /><br />
      <el-button  @click="menu()"> 返回</el-button>
    </el-aside>
    
      <el-main>
       <el-table
    :data="tableData"
    style="width: 100%">
    <el-table-column
      label="序号"
      width="100">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.rank }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="任务名"
      width="200">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.name }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="积分"
      width="100">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.points }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="截止时间"
      width="150">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.time }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="加急"
      width="100">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.fast }}</span>
      </template>
    </el-table-column>
    <el-table-column label="操作">
      <template slot-scope="scope">
        <el-popover
  placement="top"
  width="100"
  v-model="visible">
  <p>确认接受任务？</p>
  <div style="text-align: right; margin: 0">
    <el-button size="mini" type="text" @click="visible = false">取消</el-button>
    <el-button type="primary" size="mini" @click="handleAccept(scope.$index, scope.row)">确定</el-button>
  </div>
  <el-button
	  slot="reference"
          size="mini"
          @click="visible = true">接受任务</el-button>
</el-popover>
   </template>     
    </el-table-column>
    
  </el-table>
      </el-main>
    </el-container>
  </el-container>
</template>

<style>
.el-header {
  background-color: #c1cbd8;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
.logo{
    font-family:"Microsoft YaHei";
	font-size:24px;
	color:#33CCFF;
	background-color:#CCFFFF
}
.mainbt{
	width: 300px;
	height: 150px;
	margin:40px
}
</style>

<script>
export default {
  name: 'UserIndex',
  data() {
      return {
        url: 'https://images.cnblogs.com/cnblogs_com/wshope/1756522/o_200504090357logo.png',
        visible: false,
         tableData:[{
          rank: '1',
          name: '11111111',
          points: '100',
          time:'10',
          fast:'否',
        }]
      }
    },
    methods:{
    	select1(){
    	
    	},
    	select2(){
    	
    	},
    	select3(){
    	
    	},
    	select4(){
    	
    	},
    	select5(){
    	
    	},
    	handleAccept(index,row){
    	this.visible=false;
    	
    	},
    	menu(){
    this.$router.push('userindex');
    }
    }
    
    
}
</script>


