<template>
  <el-container style="border:2px solid #C0C0C0;border-radius:10px;height: 600px; width: 1200px; margin: auto">
    <!-- 页面顶部 -->
    <el-header class="logo">
<el-image
      style="width: 55px; height: 55px;margin:5px;float:left"
      :src="url"
      :fit="contain"></el-image>
<div style="float:left">发布任务
</div>
    </el-header>
<el-container style="height: 500px; width: 1200px; margin: auto">
      <el-aside width="200px"
              style="background-color:rgb(238, 241, 246)">
      <br /><br />
      <el-button  @click="menu()"> 返回 </el-button>
    </el-aside>
    
      <el-main>
       <div>
       <el-form ref="form" :model="form" label-width="120px">
  <el-form-item label="任务名称" >
    <el-input v-model="form.name" placeholder="请输入简单描述（取货地址与目的地）"></el-input>
  </el-form-item>
  <el-form-item label="任务描述">
    <el-input type="textarea" v-model="form.desc" placeholder="请输入任务描述（50汉字以内，标注姓名、取货码等必须信息）"></el-input>
  </el-form-item>
  <el-form-item label="支付积分">
    <el-col :span="5">
      <el-input v-model="form.score" placeholder="请输入支付积分"></el-input>
    </el-col>
    </el-form-item>
  <el-form-item label="截止时间">
    <el-col :span="5">
      <el-input v-model="form.time" placeholder="请输入截止时间（分钟）"></el-input>
    </el-col>
  </el-form-item>
  <el-form-item label="发布为加急">
    <el-switch v-model="form.fast" ></el-switch>
  </el-form-item>
  <el-form-item label="服务类型">
    <el-select v-model="form.type" placeholder="请选择服务类型" >
      <el-option label="快递代取" value="1"></el-option>
      <el-option label="文件代领" value="2"></el-option>
      <el-option label="食堂代买" value="3"></el-option>
      <el-option label="物品代购" value="4"></el-option>
      <el-option label="其他" value="5"></el-option>
    </el-select>
  </el-form-item>
 
  <el-form-item>
    <el-button type="primary" @click="onSubmit">立即发布</el-button>
    <el-button @click="menu()">取消</el-button>
  </el-form-item>
</el-form>
       </div>
      </el-main>
    </el-container>
  </el-container>
</template>

<style>
.el-header {
  background-color: #c1cbd8;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
.logo{
    font-family:"Microsoft YaHei";
	font-size:24px;
	color:#33CCFF;
	background-color:#CCFFFF
}
.mainbt{
	width: 300px;
	height: 150px;
	margin:40px
}
</style>

<script>
export default {
  name: 'UserIndex',
  data() {
      return {
        url: 'https://images.cnblogs.com/cnblogs_com/wshope/1756522/o_200504090357logo.png',
        form: {
          name: '',
          desc: '',
          score:'',
          time:'',
          fast:'',
          type:[]
        }
      }
    },
    methods:{
    menu(){
    this.$router.push('userindex');
    },
    onSubmit() {
    
    this.$notify({
          title: '成功',
          message: '任务发布成功',
          type: 'success'
        });
        console.log('submit!');
        this.$router.push('userindex');
      }
    }
    
    
}
</script>


