<template>
  <el-container style="border:2px solid #C0C0C0;border-radius:10px;height: 600px; width: 1200px; margin: auto">
    <!-- 页面顶部 -->
    <el-header class="logo">
<el-image
      style="width: 55px; height: 55px;margin:5px;float:left"
      :src="url"
      :fit="contain"></el-image>
<div style="float:left">我接受的任务
</div>
    </el-header>
<el-container style="height: 500px; width: 1200px; margin: auto">
      <el-aside width="200px"
              style="background-color:rgb(238, 241, 246)">
      <br /><br />
      <br /><br />
      <el-button  @click="menu()"> 返回</el-button>
    </el-aside>
    
      <el-main style="height: 500px; width: 1000px; margin: auto">
       <div style="height: 200px; width: 800px; margin: auto">
        <el-table
    :data="tableData1"
    style="width: 100%">
    <el-table-column
      label="序号"
      width="100">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.rank }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="任务名"
      width="200">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.name }}</span>
      </template>
    </el-table-column>
    
    <el-table-column
      label="剩余时间(分钟)"
      width="150">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.time }}</span>
      </template>
    </el-table-column>
    
    <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button size="mini"  type="primary" @click="">处理任务</el-button>
   </template>     
    </el-table-column>
    
  </el-table>
       </div>
       <el-divider></el-divider>
       <div style="height: 200px; width: 800px; margin: auto">
      <el-table
    :data="tableData2"
    style="width: 100%">
    <el-table-column
      label="序号"
      width="100">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.rank }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="任务名"
      width="200">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.name }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label="发布者是否确认收到"
      width="200">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.acp }}</span>
      </template>
    </el-table-column>
    <el-table-column label="操作">
      <template slot-scope="scope">
        
<el-button size="mini"  type="text" @click="">举报</el-button>
   </template>     
    </el-table-column>
    
  </el-table>
      </div>
      </el-main>
    </el-container>
  </el-container>
</template>

<style>
.el-header {
  background-color: #c1cbd8;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
.logo{
    font-family:"Microsoft YaHei";
	font-size:24px;
	color:#33CCFF;
	background-color:#CCFFFF
}
.mainbt{
	width: 300px;
	height: 150px;
	margin:40px
}
</style>

<script>
export default {
  name: 'UserIndex',
  data() {
      return {
        url: 'https://images.cnblogs.com/cnblogs_com/wshope/1756522/o_200504090357logo.png',
        tableData1:[{
          rank: '1',
          name: '11111111',
          time:'10',
          acp:'否',
        }],
        tableData2:[{
          rank: '1',
          name: '12111111',
          time:'10',
          acp:'是',
        }]
        
      }
    },
    methods:{
    menu(){
    this.$router.push('userindex');
    },
    }
    
    
}
</script>


