import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Register from '@/components/Register'
import AdminLogin from '@/components/AdminLogin'
import AdminIndex from '@/components/AdminIndex'
import UserManage from '@/components/UserManage'
import ReportManage from '@/components/ReportManage'
import RequireManage from '@/components/RequireManage'
import ReportDetail from '@/components/ReportDetail'
import RequireDetail from '@/components/RequireDetail'
import FeedbackManage from '@/components/FeedbackManage'
import FeedbackDetail from '@/components/FeedbackDetail'
import UserIndex from '@/components/UserIndex'
import Charge from '@/components/Charge'
import Postal from '@/components/Postal'
import Release from '@/components/Release'
import Accept from '@/components/Accept'
import Myrelease from '@/components/Myrelease'
import Myaccept from '@/components/Myaccept'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
        path: '/register',
        name: 'Register',
        component: Register
      },
    {
        path: '/adminlogin',
        name: 'AdminLogin',
        component: AdminLogin
      },
    {
      path: '/admin',
      name: 'AdminIndex',
      component: AdminIndex
    },
    {
      path: '/user',
      name: 'UserManage',
      component: UserManage
    },
    {
        path: '/charge',
        name: 'Charge',
        component: Charge
      },
      {
          path: '/postal',
          name: 'Postal',
          component: Postal
        },
        {
            path: '/release',
            name: 'Release',
            component: Release
          },
          {
              path: '/accept',
              name: 'Accept',
              component: Accept
            },
            {
                path: '/myrelease',
                name: 'Myrelease',
                component: Myrelease
              },
              {
                  path: '/myaccept',
                  name: 'Myaccept',
                  component: Myaccept
                },
    {
        path: '/userindex',
        name: 'UserIndex',
        component: UserIndex
      },
    {
      path: '/report',
      name: 'ReportManage',
      component: ReportManage
    },
    {
        path: '/feedback',
        name: 'FeedbackManage',
        component: FeedbackManage
      },
      
    {
        path: '/require',
        name: 'RequireManage',
        component: RequireManage
      },
      {
          path: '/feedbackdetail',
          name: 'FeedbackDetail',
          component: FeedbackDetail
        },
      {
          path: '/reportdetail',
          name: 'ReportDetail',
          component: ReportDetail
        },
        {
            path: '/requiredetail',
            name: 'RequireDetail',
            component: RequireDetail
          }

      

  ]
})


